import { Formik, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import React, { useRef } from 'react';
import sprite from '../../../images/icons/sprite.svg';
import {
  FormSellMoreDetails,
  BlocksWrapper,
  SexPhotoblock,
  Sexblock,
  RadioButton,
  IconSex,
  PhotoBlock,
  ImagePreview,
  DefaultImage,
  PhotoInput,
  PhotoLabel,
  UploadIcon,
  LocationPriceBlock,
  DetailWrapper,
  CommentsLabel,
  CommentsInput,
  Label,
} from './SellMoreDetailsForm.styled';
import BackgroundCard from '../BackgroundCard';
import TitleComponent from '../../TitleComponent/TitleComponent';
import StepsBlock from '../StepsBlock';
import {
  ButtonBlue,
  ButtonWhite,
  BtnIcon,
} from '../ButtonsBlock/ButtonsBlock.styled';
import PreviewImage from '../ImageForm/PreviewImage';

const SUPPORTED_FORMATS = ['image/png', 'image/jpeg', 'image/jpg'];

const stepThreeValidationSchema = Yup.object().shape({
  file: Yup.mixed()
    .nullable()
    .required('Select a file')
    .test(
      'FILE_SIZE',
      'Uploaded file must be 3MB or less',
      value => !value || (value && value.size <= 3145728)
    )
    .test(
      'FILE_FORMAT',
      'Uploaded file has unsupported format',
      value => !value || (value && SUPPORTED_FORMATS.includes(value?.type))
    ),

  sex: Yup.string().required('Select gender').oneOf(['male', 'female']),

  location: Yup.string().required('Enter location'),

  price: Yup.number()
    .required('Enter price')
    .min(1, 'The price must be greater than 0'),

  comments: Yup.string().max(120, 'max 120 symbols'),
});

const SellMoreDetailsForm = ({ next, prev, data }) => {
  const handleSubmit = values => {
    next(values, true);
  };
  const fileRef = useRef(null);

  return (
    <BackgroundCard width="882px">
      <TitleComponent name="Add pet" />
      <StepsBlock step={3} />
      <Formik
        validationSchema={stepThreeValidationSchema}
        initialValues={data}
        onSubmit={handleSubmit}
      >
        {({ values, setFieldValue }) => (
          <FormSellMoreDetails>
            <BlocksWrapper>
              <SexPhotoblock>
                <Sexblock role="group" aria-labelledby="gender-radio-group">
                  <h3>The Sex</h3>
                  <Label>
                    <IconSex>
                      <use href={`${sprite}#icon-female`} />
                    </IconSex>
                    <RadioButton type="radio" name="sex" value="female" />
                    Female
                  </Label>

                  <Label>
                    <IconSex>
                      <use href={`${sprite}#icon-male`} />
                    </IconSex>
                    <RadioButton type="radio" name="sex" value="male" />
                    Male
                  </Label>

                  <ErrorMessage name="sex" />
                </Sexblock>

                <PhotoBlock>
                  <PhotoLabel htmlFor="file">Load the pet’s image:</PhotoLabel>

                  <ImagePreview>
                    <PhotoInput
                      ref={fileRef}
                      hidden
                      type="file"
                      id="file"
                      name="file"
                      onChange={event => {
                        setFieldValue('file', event.target.files[0]);
                      }}
                    />
                    {values.file !== null ? (
                      <PreviewImage file={values.file} />
                    ) : (
                      <DefaultImage>
                        <UploadIcon>
                          <use href={`${sprite}#icon-plus`} />
                        </UploadIcon>
                      </DefaultImage>
                    )}
                  </ImagePreview>
                  <ErrorMessage name="file" />
                </PhotoBlock>
              </SexPhotoblock>

              <LocationPriceBlock>
                <DetailWrapper>
                  <label htmlFor="location"> Location</label>
                  <Field
                    id="location"
                    name="location"
                    placeholder="Kyiv"
                  ></Field>
                  <ErrorMessage name="location" />
                </DetailWrapper>

                {values.category === 'sell' && (
                  <DetailWrapper>
                    <label htmlFor="price"> Price</label>
                    <Field
                      id="price"
                      name="price"
                      placeholder="000 USD"
                    ></Field>
                    <ErrorMessage name="price" />
                  </DetailWrapper>
                )}

                <DetailWrapper>
                  <CommentsLabel htmlFor="comments"> Comments</CommentsLabel>
                  <CommentsInput
                    id="comments"
                    name="comments"
                    placeholder="Enter your comment"
                  ></CommentsInput>
                  <ErrorMessage name="comments" />
                </DetailWrapper>
              </LocationPriceBlock>
            </BlocksWrapper>

            <ButtonWhite type="button" onClick={() => prev(values)}>
              <BtnIcon>
                <use href={`${sprite}#icon-arrow-left`} />
              </BtnIcon>
              Back
            </ButtonWhite>
            <ButtonBlue type="submit">
              Done
              <BtnIcon>
                <use href={`${sprite}#icon-pawprint-1`} />
              </BtnIcon>
            </ButtonBlue>
          </FormSellMoreDetails>
        )}
      </Formik>
    </BackgroundCard>
  );
};

export default SellMoreDetailsForm;

